import React, { useState } from "react";
import { Nav } from "react-bootstrap";
import "./EventSubCategoryNav.css";
import { subCategoryList } from "../../constants/eventConstants";

export default function EventSubCategoryNav(props) {
  // const subCategoryList = ["Upcoming", "Archived", "All Time Favourites"];
  const [selectedSubCategory, setSelectedSubCategory] = useState(
    subCategoryList[0]
  );
  const handleSubCategorySelect = (subCategoryName) => {
    setSelectedSubCategory(subCategoryName);
    props.handleSelectedSubCategory(subCategoryName);
  };
  return (
    <div>
      <Nav
        defaultActiveKey="Account"
        className="flex-nowrap event-sub-category-list-container"
        bg="dark"
      >
        {subCategoryList.map((subCategory, key) => {
          return (
            <Nav.Item key={key}>
              <Nav.Link
                className={`event-sub-category ${
                  selectedSubCategory === subCategory &&
                  "event-sub-category-selected"
                }`}
                eventKey="Upcoming"
                onClick={() => handleSubCategorySelect(subCategory)}
              >
                {subCategory}
              </Nav.Link>
            </Nav.Item>
          );
        })}
      </Nav>
    </div>
  );
}
