import React, { useState, useEffect } from "react";
import _ from "lodash";
import "./Events.css";
import { getEventsData } from "../../api/eventsData";
import { getAllEventTags } from "../../api/eventTags";
import {
  CODING_EVENT,
  WEBINAR,
  BOOTCAMP_EVENT,
  WORKSHOP,
  ALL_EVENTS,
  UPCOMING,
  ARCHIVED,
  ALL_TIME_FAVOURITES
} from "../../constants/eventConstants";
import EventCardsList from "../EventCardsList/EventCardsList";
import TagsList from "../TagsList/TagsList";
import TagsListMobile from "../TagsListMobile/TagsListMobile";
// import EventCategoryNav from "../EventCategoryNav";
import EventCategoryNav from "../EventCategoryNav/EventCategoryNav";
import EventSubCategoryNav from "../EventSubCategoryNav/EventSubCategoryNav";
import Paging from "../Paging/Paging";

export default function Events() {
  const [allEventTags, setAllEventTags] = useState(() => []);
  const [eventCategory, setEventCategory] = useState(() => ALL_EVENTS);
  const [eventSubCategory, setEventSubCategory] = useState(() => UPCOMING);
  const [tags, setTags] = useState(() => []);
  const [offset, setOffset] = useState(() => 0);
  const [eventsData, setEventsData] = useState(() => false);
  const [showTagsInMobile, setShowTagsInMobile] = useState(false);
  // setTags(["Career Guidance","Coding Concepts", "Competitive Programming","Futuristic Tech"]);

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  function handleWindowSizeChange() {
    setWindowWidth(window.innerWidth);
  }
  useEffect(() => {
    window.addEventListener("resize", handleWindowSizeChange);
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, []);

  useEffect(() => {
    getAllEventTags().then((data) => {
      setAllEventTags(data?.data?.data?.tags);
    });
  }, []);

  useEffect(() => {
    getEventsData(eventCategory, eventSubCategory, tags, offset).then(
      (data) => {
        setEventsData(data?.data?.data);
      }
    );
  }, [eventCategory, eventSubCategory, tags, offset]);

  const handleSelectedTags = (tagText, isTagToBeAdded) => {
    if (isTagToBeAdded) {
      setTags([...tags, tagText]);
    } else {
      let newTagsList = _.remove([...tags], (tag) => tag !== tagText);
      setTags(newTagsList);
    }
  };

  const handleSelectedCategory = (selectedCategoryName) => {
    setEventCategory(selectedCategoryName);
  };

  const handleSelectedSubCategory = (selectedSubCategoryName) => {
    setEventSubCategory(selectedSubCategoryName);
  };

  const handleSelectedOffset = (selectedOffset) => {
    setOffset(selectedOffset);
  };

  const handleShowTagsInMobile = () => {
    setShowTagsInMobile(true);
  };

  return (
    <div>
      <div className="events-outer-container">
        <div className="events-container">
          <EventCategoryNav handleSelectedCategory={handleSelectedCategory} />
          <EventSubCategoryNav
            handleSelectedSubCategory={handleSelectedSubCategory}
          />
          <div className="events-tags-container">
            <div className="events-cards-list-outer-container">
              {eventsData?.events &&
                (eventsData?.events.length ? (
                  <div>
                    <EventCardsList
                      events={eventsData?.events}
                      windowWidth={windowWidth}
                    />
                    <Paging
                      totalPages={eventsData.page_count}
                      handleSelectedOffset={handleSelectedOffset}
                    />
                  </div>
                ) : (
                  "No Events Found"
                ))}
            </div>
            {allEventTags.length && (
              <div
                className={
                  windowWidth > 768
                    ? "events-tags-outer-container"
                    : `offcanvas offcanvas-end ${
                        showTagsInMobile && "show"
                      } w-100`
                }
                id={windowWidth <= 768 && "offcanvasRight"}
                style={
                  windowWidth <= 768
                    ? { visibility: "visible" }
                    : { display: "block" }
                }
              >
                {windowWidth <= 768 && (
                  <div className="offcanvas-header">
                    <h5 id="offcanvasRightLabel">Offcanvas right</h5>
                    <button
                      type="button"
                      className="btn-close text-reset"
                      onClick={() => setShowTagsInMobile(false)}
                    ></button>
                  </div>
                )}
                <div className={windowWidth <= 768 && "offcanvas-body"}>
                  <TagsList
                    tags={allEventTags}
                    handleSelectedTags={handleSelectedTags}
                    mobileView={windowWidth <= 768}
                  />
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      {windowWidth <= 768 && allEventTags && (
        <TagsListMobile handleShowTagsInMobile={handleShowTagsInMobile} />
      )}
    </div>
  );
}
