import React, { useState, useEffect } from "react";
import Tag from "../Tag/Tag";
import "./TagsList.css";

export default function Tags(props) {
  const [showTags, setShowTags] = useState(12);

  useEffect(() => {
    if (props.mobileView) setShowTags(props.tags.length);
  }, [props.mobileView, props.tags.length]);

  console.log(props.mobileView, showTags, props.tags);
  const handleShowMoreTags = () => {
    if (showTags !== 12) setShowTags(12);
    else setShowTags(props.tags.length);
  };

  return (
    <div className="tags-list-container">
      <div className="tags-list-heading">Tags</div>
      <div className="tags-container">
        {props.tags.slice(0, showTags).map((tagText, key) => {
          return (
            <Tag
              text={tagText}
              key={key}
              handleSelectedTags={props.handleSelectedTags}
            />
          );
        })}
      </div>
      {!props.mobileView && props.tags.length > 12 && (
        <div className="see-more-tags" onClick={handleShowMoreTags}>
          {showTags > 12
            ? `See less tags`
            : `See ${props.tags.length - 12} more tags`}
        </div>
      )}
    </div>
  );
}
