import React, { useState } from "react";
import { Nav } from "react-bootstrap";
import "./EventCategoryNav.css";
import { categoryList } from "../../constants/eventConstants";

export default function EventCategoryNav(props) {
  const [selectedCategory, setSelectedCategory] = useState(
    categoryList[0].name
  );
  const handleCategorySelect = (categoryName) => {
    setSelectedCategory(categoryName);
    props.handleSelectedCategory(categoryName);
  };

  return (
    <div>
      <div className="events-bar">
        <Nav
          className="nav-elements-container flex-nowrap"
          defaultActiveKey="/home"
        >
          {categoryList.map((category, key) => {
            return (
              <Nav.Item
                key={key}
                onClick={() => handleCategorySelect(category.name)}
              >
                <Nav.Link
                  className={`nav-elements-c ${
                    selectedCategory === category.name &&
                    "event-category-selected"
                  }`}
                >
                  <img
                    alt="something"
                    className="nav-elements-img"
                    src={
                      selectedCategory === category.name
                        ? category.selectedImage
                        : category.unselectedImage
                    }
                  />
                  {category.value}
                </Nav.Link>
              </Nav.Item>
            );
          })}
        </Nav>
      </div>
    </div>
  );
}
