import _ from "lodash";
import Axios from "axios";

export const getEventsData = async (
  eventCategory,
  eventSubCategory,
  tags,
  offset
) => {
  // console.log(
  //   eventCategory,
  //   eventSubCategory,
  //   _.replace(_.join(tags, ","), new RegExp(" ", "g"), "%20"),
  //   offset
  // );

  const params = {
    event_category: eventCategory,
    event_sub_category: eventSubCategory,
    tag_list: _.join(tags, ","),
    offset: offset
  };

  return await Axios.get("https://api.codingninjas.com/api/v3/events", {
    params
  })
    // .then((data) => console.log(data))
    .catch((err) => console.log(err));
};
