import React, { useState, useEffect } from "react";
import "./EventCard.css";
import { Card, Badge, Tooltip, OverlayTrigger } from "react-bootstrap";
import _ from "lodash";

export default function Eventcard(props) {
  const renderTooltip = (props, text) => (
    <Tooltip id="button-tooltip" {...props}>
      {text}
    </Tooltip>
  );

  const eventStartTime = new Date(
    props?.event.event_start_time
  ).toLocaleString("de-DE", { timeStyle: "short", hour12: true });
  const eventStartDate = new Date(
    props?.event.event_start_time
  ).toLocaleString("en-IE", { dateStyle: "medium" });
  const registrationEndTime = new Date(
    props?.event.registration_end_time
  ).toLocaleString("de-DE", { timeStyle: "short", hour12: true });
  const registrationEndDate = new Date(
    props?.event.registration_end_time
  ).toLocaleString("en-IE", { dateStyle: "medium" });
  return (
    <div className="event-card-container">
      <Card className="event-card-c">
        <Card.Img
          variant="top"
          src={
            props.windowWidth > 768
              ? props?.event.cover_picture
              : props?.event?.mobile_cover_picture
          }
          style={{ maxWidth: "100%" }}
          alt={props?.event.name}
        />
        <Card.Body className="card-body-c">
          <Card.Title className="card-title-c">{props?.event.name}</Card.Title>
          <div className="event-details">
            <div className="event-details-container">
              <p className="card-details-label"> Starts from</p>
              <p className="card-details-label-value">
                {" "}
                {`${eventStartTime}, ${eventStartDate}`}
              </p>
            </div>
            <div className="event-details-container">
              <p className="card-details-label"> Entry fee </p>
              <p className="card-details-label-value">
                {" "}
                {props?.event?.fees
                  ? `${props?.event?.currency} ${props?.event?.fees}`
                  : "Free"}{" "}
              </p>
            </div>
            <div className="event-details-container">
              <p className="card-details-label"> Venue </p>
              <p className="card-details-label-value">{props?.event?.venue} </p>
            </div>
          </div>

          <hr />
          <Card.Text className="card-text-c">
            {props?.event?.short_desc}
          </Card.Text>

          <div className="card-badge-container">
            {_.map(
              props?.event?.card_tags.slice(
                0,
                Math.min(3, props?.event?.card_tags.length)
              ),
              (val, key) => {
                return (
                  <div key={key}>
                    <Badge className="card-badge-c" variant="primary">
                      {val}
                    </Badge>{" "}
                  </div>
                );
              }
            )}
            {props?.event?.card_tags.length > 3
              ? `+${props?.event?.card_tags.length - 3} more`
              : null}
          </div>
        </Card.Body>

        <Card.Footer>
          <div className="card-footer-container">
            <div className="card-participants">
              <div className="card-participant-img">
                {_.map(props.event.registered_users.top_users, (val, key) => {
                  return (
                    <OverlayTrigger
                      key={key}
                      placement="top"
                      delay={{ show: 0, hide: 0 }}
                      overlay={(props) => renderTooltip(props, val.name)}
                    >
                      <img
                        className="participant-img"
                        src={
                          val.image_url
                            ? val.image_url
                            : "https://files.codingninjas.in/0000000000001270.png"
                        }
                        alt=""
                      />
                    </OverlayTrigger>
                  );
                })}
              </div>
              {props?.event?.registered_users?.show_users_count && (
                <p className="participant-data">
                  and{" "}
                  <span style={{ fontWeight: "700", color: "#000" }}>
                    {props?.event?.registered_users?.other_users_count}
                  </span>{" "}
                  others participating{" "}
                </p>
              )}
            </div>
            {props?.event?.registration_status === "REGISTRATIONS_OPEN" && (
              <div>
                <button className="card-button-c">REGISTER NOW</button>
              </div>
            )}
          </div>
        </Card.Footer>
      </Card>
    </div>
  );
}
