import React, { useState } from "react";
import "./Paging.css";

export default function Paging(props) {
  const [pageNumber, setPageNumber] = useState(() => {
    return 1;
  });

  const handlePageNumberChangeOnEnter = (event) => {
    if (event.target.value >= 1 && event.target.value <= props.totalPages) {
      props.handleSelectedOffset(parseInt(event.target.value) - 1);
      setPageNumber(parseInt(event.target.value));
    } else {
      props.handleSelectedOffset(1);
      document.getElementById("page-number-input").value = 1;
      setPageNumber(1);
    }
  };

  const handlePageNumberIncrease = () => {
    if (pageNumber < props.totalPages) {
      props.handleSelectedOffset(pageNumber);
      document.getElementById("page-number-input").value = pageNumber + 1;
      setPageNumber(pageNumber + 1);
    }
  };
  const handlePageNumberDecrease = () => {
    if (pageNumber > 1) {
      props.handleSelectedOffset(pageNumber - 2);
      document.getElementById("page-number-input").value = pageNumber - 1;
      setPageNumber(pageNumber - 1);
    }
  };

  return (
    <div className="paging-cotnaienr">
      <div
        className={`nav-arrow-container ${pageNumber <= 1 && "arrow-disabled"}`}
        onClick={handlePageNumberDecrease}
      >
        <img
          className="left-arrow-img"
          src="https://files.codingninjas.in/left-arrow-5581.svg"
          alt="left arrow"
        />
      </div>

      <div className="page-number-container">
        Page
        <input
          type="number"
          step="1"
          min="1"
          placeHolder="1"
          max={props.totalPages}
          onKeyUp={(event) =>
            event.key === "Enter" && handlePageNumberChangeOnEnter(event)
          }
          className="page-number-input"
          id="page-number-input"
        />
        of {props.totalPages}
      </div>

      <div
        className={`nav-arrow-container ${
          pageNumber >= props.totalPages && "arrow-disabled"
        }`}
        onClick={handlePageNumberIncrease}
      >
        <img
          className="right-arrow-img"
          src="https://files.codingninjas.in/right-arrow-5582.svg"
          alt="left arrow"
        />
      </div>
    </div>
  );
}
